body {
  margin: 8px;
  font-family: sans-serif;
}

.output {
  text-align: center;
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 700;
}

.color-palette {
  grid-template-columns: repeat(auto-fit, minmax(40px, 1fr));
  gap: 4px;
  display: grid;
}

.color-palette .item {
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 0;
  padding: 0;
  transition: transform .25s ease-in-out;
}

.color-palette .item:hover {
  transform: scale(1.1);
}

